import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { New } from '../../classes/new';
import { NewService } from '../../angular-services/new.service';

@Component({
  selector: 'app-pagina-noticia',
  templateUrl: './pagina-noticia.component.html',
  styleUrls: ['./pagina-noticia.component.css']
})
export class PaginaNoticiaComponent implements OnInit {

  news: New = new New();
  data: string = '';
  path: string = '';
  loading: boolean = true;

  constructor(private newService: NewService, private location: Location, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getNoticia();
  }

  getNoticia() {
    this.loading = true;
    const id = this.route.snapshot.paramMap.get('id');
    this.newService.getNew(id).subscribe(
      response => {
        this.loading = false;
        this.news = response.news;
      }
    );
  }

  goBack() {
    this.location.back();
  }

}
